.colaborador {
    width: 280px;
    text-align: center;
    justify-content: center;
    margin: 0 auto; /* Adicione esta linha para centralizar o colaborador */
}

.colaborador .cabecalho {
    background-color: #F0F0F0;
    border-radius: 10px 10px 0px 0px;
}

.colaborador img {
    width: 100px;
    border-radius: 50%;
    position: relative;
    bottom: -50px;
}

.colaborador .rodape {
    background: #FFFFFF;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
    border-radius: 0px 0px 10px 10px;
    padding-top: 90px;
    padding-bottom: 40px;
}

.colaborador .rodape h4 {
    color: #6278F7;
    font-size: 18px;
    line-height: 22px;
    font-weight: bold;
    margin-bottom: 8px;
}

.colaborador .rodape h5 {
    font-size: 18px;
    line-height: 22px;
    color: #212121;
    padding: 0 16px;
}

/* Adicione estas regras para centralizar os colaboradores */
.pos .colaboradores {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Centraliza os colaboradores horizontalmente */
    gap: 20px; /* Espa√ßo entre os colaboradores */
}
